import swAlert from "@sweetalert/with-react";
import {useHistory} from "react-router-dom";

function Buscador() {
    const history = useHistory();

    const submitHandle = (e) => {
        e.preventDefault();

        const keyword = e.currentTarget.keyword.value.trim();
        
        if (keyword.length === 0) {
            swAlert(<h5>Tienes que escribir una palabra clave</h5>);
        } else if (keyword.length < 4) {
            swAlert(<h5>Tienes que escribir mas de 4 caracteres</h5>)
        } else {
            e.currentTarget.keyword.value = "";
            history.push(`/resultados?keyword=${keyword}`)
        }
    }

    return (
        <form className="d-flex align-items-center" onSubmit={submitHandle}>
            <label className="form-label mb-0 mx-2">
                <input type="text" className="form-control" name="keyword" placeholder="Busca una pelicula o serie"/>
            </label>
            <button type="submit" className="btn btn-success">Buscar</button>

        </form>
    )
}

export default Buscador