import axios from "axios";

import swAlert from "@sweetalert/with-react";

import { useEffect, useState } from "react";
import { Link, Redirect } from "react-router-dom";

function Resultados () {

    let token = sessionStorage.getItem("token");
    
    let query = new URLSearchParams(window.location.search);
    let keyword = query.get("keyword");
    
    const [moviesResults, setMovieResults] = useState([]);

    useEffect(() => {
        const endPoint = `https://api.themoviedb.org/3/search/movie?api_key=acac6675cf83f41755586c88bbf95de5&language=es-ES&query=${keyword}`
        
        axios.get(endPoint)
            .then(response => {
                const moviesArray = response.data.results;

                if (moviesArray.length === 0){
                    swAlert(<h4>Tu busqueda no arrojo resultados</h4>)
                }
                setMovieResults(moviesArray);
            })
            .catch(error => {
                console.log(error);
            })
    }, [keyword]);

    return (
        <>
            {!token && <Redirect to="/" />}

            <h2>Buscaste: <em>{keyword}</em> </h2>

            {moviesResults.length === 0 && <h3>No hay resultados</h3>}

            <div className="row">
                {
                    moviesResults.map((oneMovie, idx) => {
                        return (
                            <div className="col-4" key={idx}>
                                <div className="card my-4">
                                    <img src={`https://image.tmdb.org/t/p/w500/${oneMovie.poster_path}`} className="card-img-top" alt="movie img" />
                                    <div className="card-body">
                                        <h5 className="card-title">{oneMovie.title}</h5>

                                        <Link to={`/detalle?movieID=${oneMovie.id}`} className="btn btn-primary">View detail</Link>
                                    </div>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </>
    )
}

export default Resultados;